import React, { FC, PropsWithChildren } from 'react';
import styled, { css } from 'styled-components';
import Theme from '../../theme';

import noop from '../../helpers/noop';

const BaseButton = styled.button`
  min-width: 64px;
  height: 36px;
  font-size: 14px;
  color: ${Theme.colours.white};
  background-color: ${Theme.colours.primary700};
  padding: 0 ${Theme.spacing(2)};
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
  outline: none;

  &:focus,
  &:hover {
    background-color: ${Theme.colours.primary800};
  }

  ${props =>
    props.secondary &&
    css`
      color: ${Theme.colours.black};
      background-color: ${Theme.colours.secondary500};
    `}

  ${props =>
    props.link &&
    css`
      color: ${Theme.colours.primary700};
      background-color: transparent;
      padding: 0 ${Theme.spacing(1)};
      border: none;
      box-shadow: none;
    `}
  
  &:disabled {
    color: ${Theme.colours.black};
    background-color: ${Theme.colours.white};
    opacity: 0.75;
    box-shadow: none;
    pointer-events: none;
  }
`;

const Button: FC<PropsWithChildren<any>> = ({
  disabled = false,
  secondary = false,
  onClick = noop,
  ...rest
}) => {
  const handleClick = e => {
    if (!disabled) {
      onClick(e);
    }
  };

  return <BaseButton disabled={disabled} onClick={handleClick} {...rest} />;
};

export default Button;
