import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import theme from '../../theme';

const Grid = styled.div`
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: ${p => p.theme.grid.outerMargin / 2}rem;
  padding-right: ${p => p.theme.grid.outerMargin / 2}rem;

  ${p =>
    p.fluid &&
    css`
      padding-left: ${p.theme.grid.outerMargin}rem;
      padding-right: ${p.theme.grid.outerMargin}rem;
    `};

  ${p =>
    !p.fluid &&
    css`
      ${['md', 'lg'].map(
        viewport =>
          p.theme.media[viewport]`
            width: ${p.theme.grid.container[viewport]}rem;
          `
      )};
    `};
`;

Grid.defaultProps = {
  theme
};

Grid.propTypes = {
  fluid: PropTypes.bool
};

export default Grid;
